.welcome-box {
  width: 404px;
  height: 620px;
  border-radius: 8px;
  z-index: 99;
  background-image: url('./img/welcome-lg.png');
  background-size: 100%;
  padding: 34px 44px;
  background-repeat: no-repeat;
  font-size: 16px;
  font-weight: normal;
  .welcome-logo {
    display: flex;

    .deri-logo,
    .arbitrum-logo {
      width: 50px;
      height: 50px;
      display: flex;
      background: rgba(255, 255, 255, 0.1);
      justify-content: center;
      align-items: center;
      border-radius: 10px;
    }

    .deri-logo {
      margin-right: 18px;
    }
  }

  .welcome-title {
    margin-top: 24px;
    font-size: 60px;
    font-weight: 700;

    div {
      // text-shadow: 0px 8px 11px rgba(17, 33, 65, 0.79);
      background: linear-gradient(180deg, #FFFFFF 17.72%, rgba(255, 255, 255, 0) 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
    .title-two{
      font-size: 75px;
      margin-top: -20px;
    }
    
  }
  .welcome-info{
    margin-top: 12px;
    .welcome-info-title{
      color: #FFFFFF;
      font-weight: 600;
    }
    ul{
      padding-left: 24px;
      margin-top: 8px;
    }
  }
  .learn-more-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 14px;
    margin-top: 24px;
    .btn-ok{
      width: 96px;
      height: 40px;
      border: 1px solid #59AE99;
      border-radius: 4px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #59AE99;
      cursor: pointer;
    }
  }
}
@media only screen and (max-width:759px) {
  .welcome-box {
    width: 375px;
    height: 664px;
    padding: 24px 32px;
    background-image: url('./img/welcome-xs.png');
    .learn-more-btn{
      margin-top: 48px;
    }
  }
}